// 
import myhttp from '../utils/request'

// 
export function apiThink (key) {
  return myhttp({
    url: '/app/v1_0/suggestion',
    method: 'GET',
    params: {
      q: key
    }
  })
}

// 
export function apiGetSearch ({ page, perpage, q }) {
  return myhttp({
    url: '/app/v1_0/search',
    method: 'GET',
    params: {
      page: page,
      per_page: perpage,
      q: q
    },
    needToken: true
  })
}
